
.selling-points-container {
    position: relative;
    width: 275px;
    height: 200px;
    margin-left: 20px;
    margin-right: 20px;
    padding: 50px 30px 0px 30px;
    background-color: white;
}


.selling-img-container {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: hsl(257, 27%, 26%);
    height: 80px;
    width: 80px;
    border-radius: 40px;
    top: -40px;
}


.selling-header-text {
    font-size: 20px;
    font-weight: 700;
}


.selling-body-text {
    font-size: 15px;
    font-weight: 500;
    color: hsl(257, 7%, 63%);
}


@media (max-width: 1281px) {
    .selling-img-container {
        left: 132px;
    }

    .selling-text-container {
        display: flex;
        flex-flow: column nowrap;
        text-align: center;
    }

    .selling-header-text {
        margin-bottom: 0;
    }
}